
var Converter = require('csvtojson').Converter;
var fs = require('fs');
var dateTimevalidation = require('../appliation/dateTimevalidation');
var txnIdValidation = require('../appliation/txnIdValidation');
var txnAmountvalidation = require('../appliation/txnAmountvalidation');

function applybusinessRules(jsonObj)
{

	var validationResults = [];

	for (var obj in jsonObj) {

	        switch (obj) {
				case 'txid':
			        if (!txnIdValidation.numberValidation(jsonObj[obj]))
			        	{
								        		
								        		var obj = {
					    'txid': jsonObj[obj],
					    'description': 'txid is not in number format',
					   
					}
					validationResults.push(obj);

			    		}
			        break;
				case 'clientid':
			        if (jsonObj[obj]=='') 
			        	{
			        				        		var obj = {
					    'txid': jsonObj[obj],
					    'description': 'client id cannot be null or empty',
					   
					}
					validationResults.push(obj);

			    		}
			        break;
				case 'txnamount': // assuming amount is number format checking if valus is Not a number
			        if (!txnAmountvalidation.amountValidation(jsonObj[obj])) 
			        	{		        	
			        		
	var obj = {
					    'txid': jsonObj[obj],
					    'description': 'txnamount is not in expected format',
					   
					}
					validationResults.push(obj);


			    		}
			        break;
				case 'product':
			       

			        break;
				case 'datetime':

					if(!dateTimevalidation.dateValidation(jsonObj[obj]))
					{
						var obj = {
					    'txid': jsonObj[obj],
					    'description': 'Date time provided is either in future or not valid',
					   
					}
					validationResults.push(obj);

					}
			        break;					
			    default:
			    	
						var obj = {
					    'txid': jsonObj[obj],
					    'description': 'unexpected key and this key is not handled',
					   
					}
					validationResults.push(obj);
			        break;
	        };


	}

return validationResults;

}

module.exports = applybusinessRules;